@import './mixin';
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: var(--color-secondary);
  font-size: 14px;
}

body {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;

  p {
    line-height: 1.5em;
  }
  
  h2 {
  
  }
  
  h3 {
    font-size: .8571rem;
    color: var(--color-grey-text);
    text-transform: uppercase;
  }
  
  h4 {
  
  }
  
  h5 {
    font-size: 0.6429rem;
    color: var(--color-grey-text);
    text-transform: uppercase;
  }
}

a{
  text-decoration: none;
}
.isvg{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0 0 40px;
}
.center{
  text-align: center;
  margin: 0 auto;
}

body.fontLoaded {
  font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

#app {
  min-height: 100%;
  min-width: 100%;
}

#main-container {
  position: relative;
  margin: 0 0 100px 0;
  margin-left: 250px;
  min-height: calc(100% - 240px);

  @media only screen and (max-width: 600px) and (min-width: 375px){
    margin-left: 0;
  }
  &--login {
    margin-left: 0;
    min-height: 100%;
    position: relative;
    margin: 100px auto;
  }
}



//colors
$color-primary: #1665d8;
$color-secondary: #f1f3f8;
$color-green: rgb(86, 214, 0);
$color-pending: #F7981C;
$color-error: rgb(224, 20, 20);
$color-grey-text: #7F8FA4;
$color-grey-text-light: rgba(127, 143, 164, .2);
$color-black-text: rgba(0, 0, 0, 0.87);
$color-dark: #2F3034;
$color-white: #FFFF;
$color-blue: #1665d8;
$color-black: #0000;

:root {
  @include root-prop(--color-primary, $color-primary);
  @include root-prop(--color-secondary, $color-secondary);
  @include root-prop(--color-green, $color-green);
  @include root-prop(--color-pending, $color-pending);
  @include root-prop(--color-error, $color-error);
  @include root-prop(--color-grey-text, $color-grey-text);
  @include root-prop(--color-grey-text-light, $color-grey-text-light);
  @include root-prop(--color-dark, $color-dark);
  @include root-prop(--color-white, $color-white);
  @include root-prop(--color-blue, $color-blue);
  @include root-prop(--color-black, $color-black);
  @include root-prop(--color-black-text, $color-black-text);
}


