///shadow mixin
@mixin shadow1($text) {
    @if $text == box {
      box-shadow: 0 1rem 2rem rgba($color-black, 0.5);
    } @else {
      text-shadow: 0 1rem 2rem rgba($color-black, 0.5);
    }
  }
  
  @mixin simple-flex-row {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  //placeholder
  @mixin placeholder-simple {
    &::-webkit-input-placeholder {
      @content;
    }
    &::-moz-placeholder {
      @content;
    }
    &:-ms-input-placeholder {
      @content;
    }
    &:-moz-placeholder {
      @content;
    }
    &::placeholder {
      @content;
    }
  }
  
  @mixin placeholder($font-size) {
    &::-webkit-input-placeholder {
      color: var(--color-black);
      font-size: $font-size;
      line-height: 16px;
    }
    &::-moz-placeholder {
      color: var(--color-black);
      font-size: $font-size;
      line-height: 16px;
    }
    &:-ms-input-placeholder {
      color: var(--color-black);
      font-size: $font-size;
      line-height: 16px;
    }
    &:-moz-placeholder {
        color: var(--color-black);
      font-size: $font-size;
      line-height: 16px;
    }
    &::placeholder {
      color: var(--color-black);
      font-size: $font-size;
      line-height: 16px;
    }
  }
  
  //transtion && animation
  @mixin default-transition {
    transition: all 0.5s ease-in;
  }
  @mixin default-animation($animation-name) {
    animation: $animation-name 0.8s ease-in;
  }
  
  //var root
  @mixin root-prop($prop: null, $value: null) {
    @if ($prop and $value) {
      #{$prop}: $value;
    }
  }
  
  //cross browser
  
  @mixin crossBrowser($property, $value) {
    -webkit-#{$property}: $value;
    -moz-#{$property}: $value;
    -ms-#{$property}: $value;
    #{$property}: $value;
  }
  
  @mixin crossBrowser-sticky() {
    position: -webkit-sticky;
    position: -moz-sticky;
    position: -ms-sticky;
    position: -o-sticky;
    position: sticky;
    @supports not (position: sticky) {
      position: -webkit-sticky !important;
    }
  }
  
  ///breakpoint
  @mixin respond($breakpoint) {
    @if $breakpoint == phone {
      @media (max-width: 600px) {
        @content;
      }
    }
  
    @if $breakpoint == tab-port {
      @media (max-width: 900px) {
        @content;
      }
    }
  
    @if $breakpoint == tab-land {
      @media (max-width: 1200px) {
        @content;
      }
    }
  
    @if $breakpoint == big-desktop {
      @media (max-width: 1800px) {
        @content;
      }
    }
  }
  